
import ViewBox from 'vux/src/components/view-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

import api from '@/assets/js/api.js'
const pulldownDefaultConfig = {
content: '',
height: 40,
autoRefresh: false,
downContent: '',
upContent: '',
loadingContent: '...',
clsPrefix: 'xs-plugin-pulldown-'
}
const pullupDefaultConfig = {
content: '',
pullUpHeight: 60,
height: 40,
autoRefresh: false,
downContent: '',
upContent: '',
loadingContent: '...',
clsPrefix: 'xs-plugin-pullup-'
}
export default {
data(){
return{
status:{
pulldownStatus : 'default',
pullupStatus : 'default'
},
pageNo:0,//0
dividerFlag:false,
dataListSum:0,//
completeFlowList:[],//
showDataAllFlowList:[],//
pullupDefaultConfig: pullupDefaultConfig,
pulldownDefaultConfig: pulldownDefaultConfig
}
},
methods: {
clickIcon(id,stateInco){
console.info(""+id+"id");
console.log("state---->"+stateInco);
let status ='';
if(stateInco===''){
status = "end";
}else{
status = "running"
}
this.$router.push({
path: '/memberHome/completeflowDetail',
query:{
id:id,
status:status
}
});
},
/**
* @param isAppend truefalse
*/
async loadCompleteFlowDetail(isAppend){//
let self = this;
var params = {};
params.state = self.$route.query.status;
params.pageNo = 0;
if(isAppend){
params.pageNo =  self.pageNo;
}
params.sort = "created-desc";
params.pageSize = "3";
let result = await api.finshedOrunfinshedList(params).then(res=>{
var resObject = res.data;
if(resObject!=null && resObject.data.length >0){
console.log(resObject.total);
console.log(resObject.data.length);
if(resObject.data.length===resObject.total){//
self.$refs.scrollerBottom.disablePullup();
self.dividerFlag = true;
}
self.dataListSum = resObject.total;
self.completeFlowList = resObject.data;
self.showDataAllFlowList = self.showDataAllFlowList.concat(self.completeFlowList);
}else{//
self.$refs.scrollerBottom.disablePullup();
self.dividerFlag = true;
}
});
},
loadMore(){
console.log(""+this.dataListSum);
if (this.showDataAllFlowList.length >= this.dataListSum) {//
this.$refs.scrollerBottom.disablePullup();
this.dividerFlag = true;
}else{//
this.pageNo = this.pageNo + 1;
this.loadCompleteFlowDetail(true);
this.$refs.scrollerBottom.donePullup();
this.dividerFlag = false;
}
},
},
mounted(){
let status = this.$route.query.status;
if(status==="running"){//
this.$store.commit('UPDATE_PAGE_TITLE','');
}else if(status=="completed"){
this.$store.commit('UPDATE_PAGE_TITLE','');
}
this.loadCompleteFlowDetail(false);
this.$nextTick(() => {
this.$refs.scrollerBottom.reset({top: 0})
});
},
components:{
ViewBox,
Flexbox,
FlexboxItem,
Scroller,
Divider
}
}



