
import api from '@/assets/js/api.js'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

const pulldownDefaultConfig = {
content: '',
height: 40,
autoRefresh: false,
downContent: '',
upContent: '',
loadingContent: '...',
clsPrefix: 'xs-plugin-pulldown-'
}
const pullupDefaultConfig = {
content: '',
pullUpHeight: 60,
height: 40,
autoRefresh: false,
downContent: '',
upContent: '',
loadingContent: '...',
clsPrefix: 'xs-plugin-pullup-'
}
export default {
data(){
return{
status:{
pulldownStatus : 'default',
pullupStatus : 'default'
},
pageNo:0,//0
dividerFlag:false,
dataListSum:0,//
completeFlowList:[],//
showDataAllFlowList:[],//
pullupDefaultConfig: pullupDefaultConfig,
pulldownDefaultConfig: pulldownDefaultConfig
}
},
methods: {
clickIconApproval(processInstanceId,name,id){
console.info(""+id+"id");
console.info("name-------->"+name);
let status ='';
this.$router.push({
path: '/memberHome/approvalDetail',
query:{
processInstanceId:processInstanceId,
flowname:name,
id:id
}
});
},
/**
* @param isAppend truefalse
*/
async loadApprovalFlowList(isAppend){//
let self = this;
let params = {};
params.pageNo = 0;
if(isAppend){
params.pageNo =  self.pageNo;
}
params.pageSize = 3;
let result = await api.getApprovalFlowList(params).then(res=>{
var resObject = res.data;
console.log(resObject.list.length);
if(resObject!=null && resObject.list.length >0){
console.log(resObject.count);
console.log(resObject.list.length);
if(resObject.list.length===resObject.count){//
self.$refs.scrollerBottom.disablePullup();
self.dividerFlag = true;
}
self.dataListSum = resObject.count;
self.completeFlowList = resObject.list;
self.showDataAllFlowList = self.showDataAllFlowList.concat(self.completeFlowList);
}else{//
self.$refs.scrollerBottom.disablePullup();
self.dividerFlag = true;
}
});
},
loadMore(){
console.log(""+this.dataListSum);
if (this.showDataAllFlowList.length >= this.dataListSum) {//
this.$refs.scrollerBottom.disablePullup();
this.dividerFlag = true;
}else{//
this.pageNo = this.pageNo + 1;
this.loadApprovalFlowList(true);
this.$refs.scrollerBottom.donePullup();
this.dividerFlag = false;
}
},
},
mounted(){
this.$store.commit('UPDATE_PAGE_TITLE','');
this.loadApprovalFlowList(false);
this.$nextTick(() => {
this.$refs.scrollerBottom.reset({top: 0})
});
},
components:{
ViewBox,
Flexbox,
FlexboxItem,
Scroller,
Divider
}
}



